import axios from "axios";
import {
  TestingObject,
  ParamSearch,
  Testlist,
  SetTest,
  Hashtag,
  Choised,
} from "../models/testing.interface";

const useSearchTest = async (param: ParamSearch) => {
  const response = await axios.get<TestingObject>(
    `/testwithset?keyword=${param.keyword}&page=${param.page}&quantity=${param.quantity}`
  );

  return response.data.tests;
};

const useSetTestlistPost = async (param: string) => {
  const response = await axios.post<SetTest>(`/settestlist/?settest=${param}`);

  return response.data;
};

const useTestList = async (param: boolean) => {
  const response = await axios.get<Testlist[]>(`/testlist/`, {
    params: {
      all: param,
    },
  });

  return response.data;
};

const useTestListFull = async (param: string) => {
  const response = await axios.get<Testlist[]>(`/testlist/${param}/`, {});

  return response.data;
};

const useHashtag = async () => {
  const response = await axios.get<Hashtag[]>(`/hashtag/`);
  return response.data;
};

const useCreateList = async (param: string) => {
  const response = await axios.post<string>(`/hashtag/${param}/`);
  return response.data;
};

const useSetTestList = async (param: string, id: string) => {
  const response = await axios.put<Choised>(`/settestlist/${param}/`, {
    params: {
      id: id,
    },
  });
  return response.data;
};

const testsService = {
  useSearchTest,
  useSetTestlistPost,
  useTestList,
  useHashtag,
  useCreateList,
  useTestListFull,
  useSetTestList,
};

export default testsService;
