import { createAsyncThunk, createSlice, PayloadAction } from "@reduxjs/toolkit";
import { RootState } from "../store";

import testsService from "./services/Test.service";
import {
  Test,
  SetTest,
  ParamSearch,
  Testlist,
  Hashtag,
  Choised,
} from "./models/testing.interface";

interface AsyncState {
  answer: "";
  isLoadingSearchTest: boolean;
  isSuccessSearchTest: boolean;
  isErrorSearchTest: boolean;
  updateSuccessSearchTest: boolean;
  createSuccessSearchTest: boolean;
  deleteSuccessSearchTest: boolean;
}

interface CodeHelpState extends AsyncState {
  Test: Test;
  Tests: Test[];
  SetTest: SetTest;
  Testlist: Testlist[];
  Choise: Choised;
  Choised: Choised[];
  TestlistFull: Testlist[];
  Hashtag: Hashtag;
  HashtagList: Hashtag[];
}

const initialState: CodeHelpState = {
  Test: undefined,
  Tests: [],
  SetTest: undefined,
  Testlist: [],
  TestlistFull: [],
  Hashtag: undefined,
  HashtagList: [],
  answer: "",
  Choise: undefined,
  Choised: [],
  isLoadingSearchTest: false,
  isSuccessSearchTest: false,
  isErrorSearchTest: false,
  updateSuccessSearchTest: false,
  createSuccessSearchTest: false,
  deleteSuccessSearchTest: false,
};

export const searchTest = createAsyncThunk(
  "testing/searchTest",
  async (param: ParamSearch) => {
    try {
      return await testsService.useSearchTest(param);
    } catch (error) {
      console.log("Error: ", error);
    }
  }
);

export const testList = createAsyncThunk(
  "testing/testList",
  async (param: boolean) => {
    try {
      return await testsService.useTestList(param);
    } catch (error) {
      console.log("Error: ", error);
    }
  }
);

export const testListFull = createAsyncThunk(
  "testing/testListFull",
  async (param: string) => {
    try {
      return await testsService.useTestListFull(param);
    } catch (error) {
      console.log("Error: ", error);
    }
  }
);

export const settestlistPost = createAsyncThunk(
  "testing/useSetTestlistPost",
  async (param: string) => {
    try {
      return await testsService.useSetTestlistPost(param);
    } catch (error) {
      console.log("Error: ", error);
    }
  }
);

export const heshtag = createAsyncThunk("testing/heshtag", async () => {
  try {
    return await testsService.useHashtag();
  } catch (error) {
    console.log("Error: ", error);
  }
});

export const createList = createAsyncThunk(
  "testing/createList",
  async (param: string) => {
    try {
      return await testsService.useCreateList(param);
    } catch (error) {
      console.log("Error: ", error);
    }
  }
);

export const putSetTestList = createAsyncThunk(
  "testing/putSetTestList",
  async (param: string, id: any) => {
    try {
      return await testsService.useSetTestList(param, id);
    } catch (error: any) {
      console.log("Error: ", error);
    }
  }
);

export const testingSlice = createSlice({
  name: "testing",
  initialState,
  reducers: {
    reset: (state) => {
      state.Test = undefined;
      state.Tests = [];
      state.SetTest = undefined;
      state.Testlist = [];
      state.TestlistFull = [];
      state.Hashtag = undefined;
      state.HashtagList = [];
      state.answer = "";
      state.isLoadingSearchTest = false;
      state.isSuccessSearchTest = false;
      state.isErrorSearchTest = false;
      state.updateSuccessSearchTest = false;
      state.createSuccessSearchTest = false;
      state.deleteSuccessSearchTest = false;
    },
  },
  extraReducers: (builder) => {
    builder
      // searchTest
      .addCase(searchTest.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.Tests = [];
      })
      .addCase(searchTest.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        state.Tests = action.payload || [];
      })
      .addCase(searchTest.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.Tests = [];
      })
      // testList
      .addCase(testList.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.Testlist = [];
      })
      .addCase(testList.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        state.Testlist = action.payload || [];
      })
      .addCase(testList.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.Testlist = [];
      })
      // testListFull
      .addCase(testListFull.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.TestlistFull = [];
      })
      .addCase(testListFull.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        state.TestlistFull = action.payload || [];
      })
      .addCase(testListFull.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.TestlistFull = [];
      })
      // settestlistPost
      .addCase(settestlistPost.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.SetTest = undefined;
      })
      .addCase(settestlistPost.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        state.SetTest = action.payload || undefined;
      })
      .addCase(settestlistPost.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.SetTest = undefined;
      })
      // heshtag
      .addCase(heshtag.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.HashtagList = [];
      })
      .addCase(heshtag.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        state.HashtagList = action.payload || [];
      })
      .addCase(heshtag.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.HashtagList = [];
      })
      // heshtag
      .addCase(createList.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.answer = "";
      })
      .addCase(createList.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        // state.answer = action.payload as string;
      })
      .addCase(createList.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.answer = "";
      })
      // heshtag
      .addCase(putSetTestList.pending, (state) => {
        state.isLoadingSearchTest = true;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = false;
        state.Choise = undefined;
      })
      .addCase(putSetTestList.fulfilled, (state, action) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = true;
        state.isErrorSearchTest = false;
        state.Choise = action.payload || undefined;
      })
      .addCase(putSetTestList.rejected, (state) => {
        state.isLoadingSearchTest = false;
        state.isSuccessSearchTest = false;
        state.isErrorSearchTest = true;
        state.Choise = undefined;
      });
  },
});
export const { reset } = testingSlice.actions;

export const selectedCodeHelp = (state: RootState) => {
  return state.testing;
};

export default testingSlice.reducer;
